import React from "react";
import { IPurchaseOrder } from "../../models/purchaseOrder";
import { convertStringToDate } from "../../utilities/helpers";
import { IVendor } from "../../models/vendor";
import { ICurrency } from "../../models/currency";
import { IProduct } from "../../models/product";
import { Column } from "primereact/column";
import { DataTable } from "primereact/datatable";

interface IPurchaseOrderPrintProps {
  item: IPurchaseOrder;
  currencies: ICurrency[];
  products: IProduct[];
  vendors: IVendor[];
}

const PurchaseOrderPrint: React.FC<IPurchaseOrderPrintProps> = (props) => {
  const priceBodyTemplate = (rowData: any) => {
    return <div className="text-right">{rowData?.listPrice?.toFixed(2)}</div>;
  };

  const discountBodyTemplate = (rowData: any) => {
    return `${rowData?.discountType} ${rowData?.discount}`;
  };

  const totalBodyTemplate = (rowData: any) => {
    return <div className="text-right">{rowData?.total?.toFixed(2)}</div>;
  };

  const tableFooter = (item: IPurchaseOrder) => (
    <div className="grid text-right">
      <div className="col-7">&nbsp;</div>
      <div className="col-5">
        <div className="grid">
          <div className="col-12">
            <div className="grid">
              <div className="col">Items Total:</div>
              <div className="col">{item.subTotal?.toFixed(2)}</div>
            </div>
          </div>
          <div className="col-12">
            <div className="grid">
              <div className="col">Tax:</div>
              <div className="col">{item.taxTotal?.toFixed(2)}</div>
            </div>
          </div>
          <div className="col-12">
            <div className="grid">
              <div className="col">Grand Total: (in {item.currencyId})</div>
              <div className="col">{item.total?.toFixed(2)}</div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );

  const getContent = () => {
    if (props.item.id === "") {
      return null;
    }

    const item: IPurchaseOrder = { ...props.item };

    if (props.vendors) {
      const vendor = props.vendors.find((x) => x.id === item.vendorId);
      if (vendor) {
        item.vendorId = vendor.name;
      }
    }

    if (props.currencies) {
      const currency = props.currencies.find((x) => x.id === item.currencyId);
      if (currency) {
        item.currencyId = currency.symbol;
      }
    }

    if (props.products && props.item.items) {
      item.items = props.item.items.map((x) => {
        const result = { ...x };
        const product = props.products.find((p) => p.id === x.productId);
        if (product) {
          result.productId = product.name;
        }

        return result;
      });
    }

    return (
      <div className="grid p-5">
        <div className="col-12">
          <h3>Purchase Order</h3>
        </div>
        <div className="col-12">
          <div className="grid">
            <div className="col">
              <label htmlFor="Subject">Subject:</label>
            </div>
            <div className="col">{item.subject}</div>
            <div className="col-fixed p-fixed hidden-xs">&nbsp;</div>
            <div className="col">
              <label htmlFor="Opportunity">Vendor Name:</label>
            </div>
            <div className="col">{item.vendorId}</div>
          </div>
        </div>
        <div className="col-12">
          <div className="grid">
            <div className="col">
              <label htmlFor="CustomerNo">Issued Date:</label>
            </div>
            <div className="col">{new Date().toLocaleDateString()}</div>
            <div className="col-fixed p-fixed hidden-xs">&nbsp;</div>
            <div className="col">
              <label htmlFor="Account">Due Date:</label>
            </div>
            <div className="col">
              {convertStringToDate(item.dueDate)?.toLocaleDateString()}
            </div>
          </div>
        </div>
        <div className="col-12">
          <div className="grid">
            <div className="col">
              <label htmlFor="PurchaseOrder">Billing Address:</label>
            </div>
            <div className="col">{`${item.billingAddressStreet} ${item.billingAddressCity}, ${item.billingAddressState} ${item.billingAddressPostalcode} ${item.billingAddressCountry}`}</div>
            <div className="col-fixed p-fixed hidden-xs">&nbsp;</div>
            <div className="col">
              <label htmlFor="Contact">Shipping Address:</label>
            </div>
            <div className="col">{`${item.shippingAddressStreet} ${item.shippingAddressCity}, ${item.shippingAddressState} ${item.shippingAddressPostalcode} ${item.shippingAddressCountry}`}</div>
          </div>
        </div>
        <div className="col-12">
          <DataTable
            value={item.items}
            showGridlines
            footer={tableFooter(item)}
          >
            <Column field="productId" header="Product"></Column>
            <Column field="quantity" header="Quantity"></Column>
            <Column
              field="listPrice"
              header="Selling Price"
              body={priceBodyTemplate}
            ></Column>
            <Column
              field="discountType"
              header="Discount"
              body={discountBodyTemplate}
            ></Column>
            <Column
              field="total"
              header="Total"
              body={totalBodyTemplate}
            ></Column>
          </DataTable>
        </div>
        <div className="col-12">
          <h3>Description</h3>
        </div>
        <div className="col-12">{item.description}</div>
        <div className="col-12">
          <h3>Terms &amp; Conditions</h3>
        </div>
        <div className="col-12">{item.termsConditions}</div>
      </div>
    );
  };

  return <>{getContent()}</>;
};

export default PurchaseOrderPrint;
