import React, { useCallback, useEffect, useRef, useState } from "react";
import { useNavigate } from "react-router-dom";
import { useParams } from "react-router-dom";
import { Button } from "primereact/button";
import { Card } from "primereact/card";
import { Calendar } from "primereact/calendar";
import { Column } from "primereact/column";
import { DataTable } from "primereact/datatable";
import { Dialog } from "primereact/dialog";
import { Dropdown } from "primereact/dropdown";
import { InputText } from "primereact/inputtext";
import { InputTextarea } from "primereact/inputtextarea";
import { InputNumber } from "primereact/inputnumber";
import { Message } from "primereact/message";
import { MultiStateCheckbox } from "primereact/multistatecheckbox";
import { shallowEqual } from "react-redux";
import { useAppDispatch, useAppSelector } from "../../utilities/hooks";
import { contactActions } from "../../store/contact/contactSlice";
import { currencyActions } from "../../store/currency/currencySlice";
import { opportunityActions } from "../../store/opportunity/opportunitySlice";
import { productActions } from "../../store/product/productSlice";
import { purchaseOrderActions } from "../../store/purchaseOrder/purchaseOrderSlice";
import { userActions } from "../../store/user/userSlice";
import { vendorActions } from "../../store/vendor/vendorSlice";
import { IAppState } from "../../models/appState";
import { IInventoryProduct } from "../../models/inventoryProduct";
import { IProduct } from "../../models/product";
import { IPurchaseOrder } from "../../models/purchaseOrder";
import { v4 as uuidv4 } from "uuid";
import { TERMS_CONDITIONS } from "../../utilities/constants";
import {
  convertDateToString,
  convertStringToDate,
  convertCurrency,
} from "../../utilities/helpers";
import { discounts, statusData } from "../../utilities/globals";
import PurchaseOrderPrint from "./PurchaseOrderPrint";
import { useReactToPrint } from "react-to-print";

const PurchaseOrderEdit: React.FC = () => {
  const carrierData = ["FedEx", "UPS", "USPS", "DHL", "BlueDart"];

  const defaultItem: IPurchaseOrder = {
    id: "",
    subject: "",
    vendorId: "",
    vendorName: "",
    requisitionNo: "",
    trackingNo: "",
    contactId: "",
    dueDate: "",
    carrier: "",
    salesCommission: 0,
    exciseDuty: 0,
    termsConditions: "",
    status: "",
    currencyId: "",
    subTotal: 0,
    taxTotal: 0,
    total: 0,
    assignedUserId: "",
    assignedUserLastName: "",
    billingAddressStreet: "",
    billingAddressCity: "",
    billingAddressState: "",
    billingAddressPostalcode: "",
    billingAddressCountry: "",
    shippingAddressStreet: "",
    shippingAddressCity: "",
    shippingAddressState: "",
    shippingAddressPostalcode: "",
    shippingAddressCountry: "",
    description: "",
    items: [],
  };
  const navigateTo = "/purchaseorders";
  const navigate = useNavigate();
  const [subtitle, setSubtitle] = useState("Create");
  const [item, setItem] = useState(defaultItem);
  const [dirty, setDirty] = useState(false); // if form input has been changed, dirty is true
  const [valid, setValid] = useState(false); // if form input is valid, valid is true
  const [errors, setErrors] = useState({}); // indicates if fields are valid
  const [deleteDialog, setDeleteDialog] = useState(false);
  const [inventoryId, setInventoryId] = useState(""); // Inventory Product Id
  const [activeProducts, setActiveProducts] = useState([] as IProduct[]);
  const params = useParams();
  const memo = useRef(true);
  const componentRef = useRef(null);

  const [
    purchaseOrder,
    isFetching,
    isError,
    contacts,
    currencies,
    products,
    purchaseOrders,
    users,
    isProfile,
    profile,
    vendors,
  ] = useAppSelector(
    (state: IAppState) => [
      state.purchaseOrders.purchaseOrder,
      state.purchaseOrders.isFetching,
      state.purchaseOrders.isError,
      state.contacts.contacts,
      state.currencies.currencies,
      state.products.products,
      state.purchaseOrders.purchaseOrders,
      state.users.users,
      state.users.isProfile,
      state.users.profile,
      state.vendors.vendors,
    ],
    shallowEqual
  );
  const dispatch = useAppDispatch();

  useEffect(() => {
    if (memo.current) {
      memo.current = false;
      dispatch(contactActions.getContacts());
      dispatch(currencyActions.getCurrencies());
      dispatch(opportunityActions.getOpportunities());
      dispatch(productActions.getProducts());
      dispatch(userActions.getUsers());
      dispatch(vendorActions.getVendors());
      if (params.mode === "edit") {
        setSubtitle("Edit");
        const id = params.id;
        if (id) {
          dispatch(purchaseOrderActions.getPurchaseOrderById(id));
        }
      } else {
        setItem({ ...item, termsConditions: TERMS_CONDITIONS });
      }
    }
  }, []); // eslint-disable-line react-hooks/exhaustive-deps

  useEffect(() => {
    if (
      params.mode !== "edit" &&
      isProfile &&
      profile &&
      profile.currencyId &&
      item.currencyId === "" &&
      profile.currencyId !== item.currencyId
    ) {
      setItem({ ...item, currencyId: profile.currencyId });
    }
  }, [params, isProfile, profile, item]);

  useEffect(() => {
    if (
      params.mode === "edit" &&
      item.id !== purchaseOrder?.id &&
      !isFetching &&
      !isError
    ) {
      setItem(purchaseOrder);
    }
  }, [params, item, purchaseOrder, isFetching, isError]);

  useEffect(() => {
    if (products && products.length > 0) {
      const productItems = products.filter((x) => !!x.active);
      if (productItems.length > 0) {
        setActiveProducts(productItems);
      }
    }
  }, [products]);

  const isSubjectValid = useCallback(
    (subject: string) => {
      let result = true;
      if (item.id.length === 0) {
        if (
          purchaseOrders.find(
            (x) => x.subject?.toLowerCase() === subject?.toLowerCase()
          )
        ) {
          result = false;
        }
      } else {
        if (
          purchaseOrders.find(
            (x) =>
              x.subject?.toLowerCase() === subject?.toLowerCase() &&
              x.id !== item.id
          )
        ) {
          result = false;
        }
      }

      return result;
    },
    [item.id, purchaseOrders]
  );

  useEffect(() => {
    if (!dirty) {
      return;
    }

    setValid(
      (item.subject || "").trim().length > 0 &&
        isSubjectValid(item.subject || "") &&
        (item.currencyId || "").trim().length > 0
    );
  }, [dirty, item, isSubjectValid]);

  const convertAmounts = (currencyId: string) => {
    let itemsTotal = 0;
    let itemsTax = 0;

    const id = item.currencyId ? item.currencyId : "";
    const items = item.items || [];
    const soItems: IInventoryProduct[] = [];
    for (let i = 0; i < items.length; i++) {
      let x = { ...items[i] };
      x.purchaseCost = convertCurrency(
        id,
        currencyId,
        x.purchaseCost,
        currencies
      );
      x.listPrice = convertCurrency(id, currencyId, x.listPrice, currencies);

      if (x.discountType === "$") {
        x.discount = convertCurrency(
          id,
          currencyId,
          x.discount || 0,
          currencies
        );
      }

      x.total = convertCurrency(id, currencyId, x.total, currencies);
      x.tax = convertCurrency(id, currencyId, x.tax, currencies);
      soItems.push(x);

      itemsTotal += x.total;
      itemsTax = x.tax;
    }

    return {
      subTotal: itemsTotal,
      taxTotal: itemsTax,
      total: itemsTotal + itemsTax,
      items: soItems,
    };
  };

  const onChange = (e: any) => {
    setDirty(true);
    const {
      target: { name: tname, value: tvalue },
    } = e;

    if (tname === "dueDate") {
      setItem({ ...item, [tname]: convertDateToString(tvalue) });
    } else if (tname === "vendorId") {
      const vendor = vendors.find((value) => value.id === tvalue);
      if (vendor) {
        setItem({ ...item, [tname]: tvalue, vendorName: vendor.name });
      }
    } else if (tname === "currencyId") {
      setItem({ ...item, [tname]: tvalue, ...convertAmounts(e.target.value) });
    } else if (tname === "assignedUserId") {
      const user = users.find((value) => value.id === tvalue);
      if (user) {
        setItem({
          ...item,
          [tname]: tvalue,
          assignedUserLastName: user.lastName,
        });
      }
    } else {
      setItem({ ...item, [tname]: tvalue });
    }

    if (tname === "subject") {
      const isValidSubject = isSubjectValid(tvalue);
      setErrors({
        ...errors,
        subjectInvalid: !isValidSubject,
        [tname]: tvalue ? tvalue.toString().trim().length === 0 : true,
      });
    }
  };

  const onCopyBillingClick = () => {
    setDirty(true);
    setItem({
      ...item,
      shippingAddressStreet: item.billingAddressStreet,
      shippingAddressCity: item.billingAddressCity,
      shippingAddressState: item.billingAddressState,
      shippingAddressPostalcode: item.billingAddressPostalcode,
      shippingAddressCountry: item.billingAddressCountry,
    });
  };

  const onCopyShippingClick = () => {
    setDirty(true);
    setItem({
      ...item,
      billingAddressStreet: item.shippingAddressStreet,
      billingAddressCity: item.shippingAddressCity,
      billingAddressState: item.shippingAddressState,
      billingAddressPostalcode: item.shippingAddressPostalcode,
      billingAddressCountry: item.shippingAddressCountry,
    });
  };

  const onAddClick = () => {
    setDirty(true);

    const newItem: IInventoryProduct = {
      id: uuidv4(),
      quantity: 1,
      discountType: "",
      discount: 0,
      tax: 0,
      total: 0,
      taxes: [],
    };

    let newItemTotal = 0.0;
    let newItemTax = 0.0;

    if (activeProducts && activeProducts.length > 0) {
      const product = activeProducts[0];
      newItem.productId = product.id;
      newItem.listPrice = product.unitPrice || 0;
      newItem.purchaseCost =
        (product.purchaseCost || 0) * (newItem.quantity || 0);
      if (product.currencyId !== item.currencyId) {
        newItem.listPrice = convertCurrency(
          product.currencyId || "",
          item.currencyId || "",
          newItem.listPrice,
          currencies
        );
        newItem.purchaseCost = convertCurrency(
          product.currencyId || "",
          item.currencyId || "",
          newItem.purchaseCost,
          currencies
        );
      }

      newItem.taxes = [...(product.taxes || [])];

      // Calc taxes
      newItemTotal = newItem.listPrice;
      newItemTax = 0.0;
      if (newItem.taxes.length > 0) {
        newItem.taxes.forEach((x) => {
          newItemTax += (newItemTotal * (x.value || 0)) / 100.0;
        });
        newItem.tax = newItemTax;
      }

      newItem.total = newItemTotal;
    }

    const itemsTotal = (item.subTotal || 0) + newItemTotal;
    const itemsTax = (item.taxTotal || 0) + newItemTax;
    const total = itemsTotal + itemsTax;

    const items = [...(item.items || []), newItem];
    setItem({
      ...item,
      subTotal: itemsTotal,
      taxTotal: itemsTax,
      total: total,
      items: items,
    });
  };

  const onRowEditComplete = (e: any) => {
    setDirty(true);
    const { newData, index, data } = e;
    const rowData = newData as IInventoryProduct;
    rowData.discount = data.discount || 0;
    let itemTotal = (rowData.listPrice || 0) * (rowData.quantity || 0);

    // Calc discounts
    switch (rowData.discountType) {
      case "%":
        itemTotal -= (itemTotal * (rowData.discount || 0)) / 100.0;
        break;

      case "$":
        itemTotal -= rowData.discount || 0;
        break;

      default:
        rowData.discount = 0;
    }

    rowData.total = itemTotal;

    let itemTax = 0;
    const product = activeProducts.find((x) => x.id === rowData.productId);
    if (product) {
      rowData.purchaseCost =
        (product.purchaseCost || 0) * (rowData.quantity || 0);
      rowData.taxes = [...(product.taxes || [])];

      // Calc item tax
      if (rowData.taxes.length > 0) {
        rowData.taxes.forEach((x) => {
          itemTax += (itemTotal * (x.value || 0)) / 100.0;
        });
        rowData.tax = itemTax;
      }
    }

    // Calc totals
    let itemsTotal = itemTotal;
    let itemsTax = itemTax;
    let total = 0;
    if (item.items) {
      item.items.forEach((x) => {
        if (x.id !== rowData.id) {
          itemsTotal += x.total || 0;
          itemsTax += x.tax || 0;
        }
      });

      total = itemsTotal + itemsTax;
    }

    const soItems = [...(item.items || [])];
    soItems[index] = rowData;
    setItem({
      ...item,
      subTotal: itemsTotal,
      taxTotal: itemsTax,
      total: total,
      items: soItems,
    });
  };

  const productBodyTemplate = (rowData: any) => {
    const product = products.find((x) => x.id === rowData.productId);
    return product?.name;
  };

  const onProductEditorChange = (options: any, e: any) => {
    const product = activeProducts.find((x) => x.id === e.value);
    if (product && item) {
      const { rowData, rowIndex } = options;
      rowData.listPrice = product.unitPrice;
      const soItems = [...(item.items || [])];
      soItems[rowIndex] = rowData;
      setItem({ ...item, items: soItems });
    }

    options.editorCallback(e.value);
  };

  const productEditor = (options: any) => {
    return (
      <Dropdown
        value={options.value}
        options={activeProducts}
        optionLabel="name"
        optionValue="id"
        onChange={(e) => onProductEditorChange(options, e)}
        placeholder="Select a Product"
        itemTemplate={(option) => {
          return <span>{option.name}</span>;
        }}
      />
    );
  };

  const priceBodyTemplate = (rowData: any) => {
    return <div className="text-right">{rowData?.listPrice?.toFixed(2)}</div>;
  };

  const discountBodyTemplate = (rowData: any) => {
    return `${rowData?.discountType} ${rowData?.discount}`;
  };

  const onDiscountTypeEditorChange = (options: any, e: any) => {
    let discountType;

    switch (e.value) {
      case "% Price":
        discountType = "%";
        break;

      case "Direct Price Reduction":
        discountType = "$";
        break;

      default:
        discountType = "";
    }

    options.editorCallback(discountType);
  };

  const onDiscountEditorChange = (options: any, e: any) => {
    const { rowData, rowIndex } = options;
    rowData.discount = e;
    const soItems = [...(item.items || [])];
    soItems[rowIndex] = rowData;
    setItem({ ...item, items: soItems });
  };

  const discountEditor = (options: any) => {
    let discountType;

    switch (options.value) {
      case "%":
        discountType = "% Price";
        break;

      case "$":
        discountType = "Direct Price Reduction";
        break;

      default:
        discountType = "Zero Discount";
    }

    return (
      <div className="field-checkbox m-0">
        <MultiStateCheckbox
          value={discountType}
          options={discounts}
          optionValue="value"
          onChange={(e) => onDiscountTypeEditorChange(options, e)}
          tooltip={discountType}
        />
        &nbsp;
        {discountType !== "Zero Discount" && (
          <InputNumber
            value={options.rowData?.discount}
            onValueChange={(e) => onDiscountEditorChange(options, e.value)}
            size={9}
          />
        )}
      </div>
    );
  };

  const textEditor = (options: any) => {
    return (
      <InputText
        value={options.value}
        onChange={(e) => options.editorCallback(e.target.value)}
      />
    );
  };

  const numberEditor = (options: any) => {
    return (
      <InputNumber
        value={options.value}
        onValueChange={(e) => options.editorCallback(e.value)}
        size={9}
      />
    );
  };

  const totalBodyTemplate = (rowData: any) => {
    return <div className="text-right">{rowData?.total?.toFixed(2)}</div>;
  };

  const actionBodyTemplate = (rowData: any) => {
    return (
      <>
        <Button
          icon="pi pi-trash"
          className="p-row-editor-init p-link"
          tooltip="Delete"
          onClick={() => confirmDeleteItem(rowData)}
        />
      </>
    );
  };

  const confirmDeleteItem = (rowData: any) => {
    setInventoryId(rowData.id);
    setDeleteDialog(true);
  };

  const hideDeleteDialog = () => {
    setDeleteDialog(false);
  };

  const deleteItem = async () => {
    if (inventoryId) {
      const invItems = item.items?.filter((x) => x.id !== inventoryId);
      if (invItems) {
        setDirty(true);

        let deltemTotal = 0.0;
        let delItemTax = 0.0;
        const delItem = item.items?.find((x) => x.id === inventoryId);
        if (delItem) {
          deltemTotal = delItem.total || 0.0;
          delItemTax = delItem.tax || 0.0;
        }

        const itemsTotal = (item.subTotal || 0) - deltemTotal;
        const itemsTax = (item.taxTotal || 0) - delItemTax;
        const total = itemsTotal + itemsTax;

        setItem({
          ...item,
          subTotal: itemsTotal,
          taxTotal: itemsTax,
          total: total,
          items: invItems,
        });
      }
    }

    hideDeleteDialog();
    setInventoryId("");
  };

  const deleteDialogFooter = (
    <>
      <Button
        label="No"
        icon="pi pi-times"
        className="p-button-text"
        onClick={hideDeleteDialog}
      />
      <Button
        label="Yes"
        icon="pi pi-check"
        className="p-button-text"
        onClick={deleteItem}
      />
    </>
  );

  const onRowReorder = (e: any) => {
    setDirty(true);
    setItem({ ...item, items: e.value });
  };

  const tableFooter = (
    <div className="grid text-right">
      <div className="col-9">&nbsp;</div>
      <div className="col-3">
        <div className="grid">
          <div className="col-12">
            <div className="grid">
              <div className="col">Items Total:</div>
              <div className="col">{item.subTotal?.toFixed(2)}</div>
            </div>
          </div>
          <div className="col-12">
            <div className="grid">
              <div className="col">Tax:</div>
              <div className="col">{item.taxTotal?.toFixed(2)}</div>
            </div>
          </div>
          <div className="col-12">
            <div className="grid">
              <div className="col">Grand Total:</div>
              <div className="col">{item.total?.toFixed(2)}</div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );

  const onSaveClick = async () => {
    if (subtitle !== "Edit") {
      item.id = uuidv4();
    }

    dispatch(purchaseOrderActions.updatePurchaseOrder(item));
    setTimeout(() => {
      dispatch(purchaseOrderActions.getPurchaseOrders());
    }, 300);
    navigate(navigateTo);
  };

  const onCancelClick = async () => {
    navigate(navigateTo);
  };

  const onPrintClick = useReactToPrint({
    content: () => componentRef.current,
  });

  return (
    <>
      <div>
        <h3 className="caption">Purchase Order: {subtitle}</h3>
        <div className="grid">
          <div className="col-12">
            <span className="field-required">*</span> - Required field
          </div>
        </div>
        <Card>
          <div className="grid">
            <div className="col-12">
              <h3>Purchase Order</h3>
            </div>
          </div>
          <div className="grid">
            <div className="col-12">
              <div className="grid">
                <div className="col">
                  <label htmlFor="Subject">
                    Subject <span className="field-required">*</span>
                  </label>
                </div>
                <div className="col">
                  <InputText
                    id="Subject"
                    name="subject"
                    maxLength={100}
                    placeholder="Subject"
                    value={item.subject}
                    onChange={onChange}
                  />
                  {(errors as any)?.subject && (
                    <Message severity="error" text="Subject is required." />
                  )}
                  {(errors as any)?.subjectInvalid && (
                    <Message severity="error" text="Subject already exists." />
                  )}
                </div>
                <div className="col-fixed p-fixed hidden-xs">&nbsp;</div>
                <div className="col">
                  <label htmlFor="Vendor">Vendor</label>
                </div>
                <div className="col">
                  <div className="p-fixed-switch">
                    <Dropdown
                      id="Vendor"
                      name="vendorId"
                      options={vendors}
                      optionLabel="name"
                      optionValue="id"
                      value={item.vendorId}
                      onChange={onChange}
                      className="p-combobox"
                    />
                  </div>
                </div>
              </div>
            </div>
            <div className="col-12">
              <div className="grid">
                <div className="col">
                  <label htmlFor="TrackingNo">Tracking No</label>
                </div>
                <div className="col">
                  <InputText
                    id="TrackingNo"
                    name="trackingNo"
                    maxLength={100}
                    value={item.trackingNo}
                    onChange={onChange}
                  />
                </div>
                <div className="col-fixed p-fixed hidden-xs">&nbsp;</div>
                <div className="col">
                  <label htmlFor="RequisitionNo">Requisition No</label>
                </div>
                <div className="col">
                  <InputText
                    id="RequisitionNo"
                    name="requisitionNo"
                    maxLength={100}
                    value={item.requisitionNo}
                    onChange={onChange}
                  />
                </div>
              </div>
            </div>
            <div className="col-12">
              <div className="grid">
                <div className="col">
                  <label htmlFor="DueDate">Due Date</label>
                </div>
                <div className="col">
                  <Calendar
                    id="DueDate"
                    name="dueDate"
                    value={convertStringToDate(item.dueDate)}
                    onChange={onChange}
                    showButtonBar
                    showIcon
                    className="p-combobox"
                  />
                </div>
                <div className="col-fixed p-fixed hidden-xs">&nbsp;</div>
                <div className="col">
                  <label htmlFor="Contact">Contact</label>
                </div>
                <div className="col">
                  <Dropdown
                    id="Contact"
                    name="contactId"
                    options={contacts}
                    optionLabel="lastName"
                    optionValue="id"
                    value={item.contactId}
                    onChange={onChange}
                    className="p-combobox"
                  />
                </div>
              </div>
            </div>
            <div className="col-12">
              <div className="grid">
                <div className="col">
                  <label htmlFor="SalesCommission">Sales Commission</label>
                </div>
                <div className="col">
                  <InputNumber
                    id="SalesCommission"
                    name="salesCommission"
                    value={item.salesCommission}
                    onValueChange={onChange}
                  />
                </div>
                <div className="col-fixed p-fixed hidden-xs">&nbsp;</div>
                <div className="col">
                  <label htmlFor="Carrier">Carrier</label>
                </div>
                <div className="col">
                  <Dropdown
                    id="Carrier"
                    name="carrier"
                    options={carrierData}
                    value={item.carrier}
                    onChange={onChange}
                    className="p-combobox"
                  />
                </div>
              </div>
            </div>
            <div className="col-12">
              <div className="grid">
                <div className="col">
                  <label htmlFor="ExciseDuty">Excise Duty</label>
                </div>
                <div className="col">
                  <InputNumber
                    id="ExciseDuty"
                    name="exciseDuty"
                    value={item.exciseDuty}
                    onValueChange={onChange}
                  />
                </div>
                <div className="col-fixed p-fixed hidden-xs">&nbsp;</div>
                <div className="col">
                  <label htmlFor="Status">Status</label>
                </div>
                <div className="col">
                  <Dropdown
                    id="Status"
                    name="status"
                    options={statusData}
                    value={item.status}
                    onChange={onChange}
                    className="p-combobox"
                  />
                </div>
              </div>
            </div>
            <div className="col-12">
              <div className="grid">
                <div className="col">
                  <label htmlFor="Currency">
                    Currency <span className="field-required">*</span>
                  </label>
                </div>
                <div className="col">
                  <Dropdown
                    id="Currency"
                    name="currencyId"
                    options={currencies}
                    optionLabel="name"
                    optionValue="id"
                    value={item.currencyId}
                    onChange={onChange}
                    className="p-combobox"
                  />
                  {(errors as any)?.currencyId && (
                    <Message severity="error" text="Currency is required." />
                  )}
                </div>
                <div className="col-fixed p-fixed hidden-xs">&nbsp;</div>
                <div className="col">
                  <label htmlFor="AssignedTo">Assigned To</label>
                </div>
                <div className="col">
                  <Dropdown
                    id="AssignedTo"
                    name="assignedUserId"
                    options={users}
                    optionLabel="lastName"
                    optionValue="id"
                    value={item.assignedUserId}
                    onChange={onChange}
                    className="p-combobox"
                  />
                </div>
              </div>
            </div>
          </div>
        </Card>
        <div className="grid">
          <div className="col-12">&nbsp;</div>
        </div>
        <Card>
          <div className="grid">
            <div className="col-12">
              <h3>Address</h3>
            </div>
          </div>
          <div className="grid">
            <div className="col">
              <h4>Billing Address</h4>
            </div>
            <div className="col-fixed p-fixed-10 hidden-xs">&nbsp;</div>
            <div className="col hidden-xs">
              <h4>Shipping Address</h4>
            </div>
          </div>
          <div className="grid">
            <div className="col-12">
              <div className="grid">
                <div className="col">
                  <label htmlFor="BillingStreet">Street</label>
                </div>
                <div className="col">
                  <InputText
                    id="BillingStreet"
                    name="billingAddressStreet"
                    maxLength={150}
                    value={item.billingAddressStreet}
                    onChange={onChange}
                  />
                </div>
                <div className="col-fixed p-fixed-10 hidden-xs">&nbsp;</div>
                <div className="col hidden-xs">
                  <label htmlFor="ShippingStreet1">Street</label>
                </div>
                <div className="col hidden-xs">
                  <InputText
                    id="ShippingStreet1"
                    name="shippingAddressStreet"
                    maxLength={150}
                    value={item.shippingAddressStreet}
                    onChange={onChange}
                  />
                </div>
              </div>
            </div>
            <div className="col-12">
              <div className="grid">
                <div className="col">
                  <label htmlFor="BillingCity">City</label>
                </div>
                <div className="col">
                  <InputText
                    id="BillingCity"
                    name="billingAddressCity"
                    maxLength={100}
                    value={item.billingAddressCity}
                    onChange={onChange}
                  />
                </div>
                <div className="col-fixed p-fixed-10 hidden-xs">
                  <Button
                    icon="pi pi-arrow-circle-right"
                    className="p-button-rounded p-button-text p-button-lg"
                    tooltip="Copy Billing Address"
                    onClick={onCopyBillingClick}
                  />
                </div>
                <div className="col hidden-xs">
                  <label htmlFor="ShippingCity1">City</label>
                </div>
                <div className="col hidden-xs">
                  <InputText
                    id="ShippingCity1"
                    name="shippingAddressCity"
                    maxLength={100}
                    value={item.shippingAddressCity}
                    onChange={onChange}
                  />
                </div>
              </div>
            </div>
            <div className="col">
              <div className="grid">
                <div className="col">
                  <label htmlFor="BillingState">State</label>
                </div>
                <div className="col">
                  <InputText
                    id="BillingState"
                    name="billingAddressState"
                    maxLength={100}
                    value={item.billingAddressState}
                    onChange={onChange}
                  />
                </div>
                <div className="col-fixed p-fixed-10 hidden-xs">&nbsp;</div>
                <div className="col hidden-xs">
                  <label htmlFor="ShippingState1">State</label>
                </div>
                <div className="col hidden-xs">
                  <InputText
                    id="ShippingState1"
                    name="shippingAddressState"
                    maxLength={100}
                    value={item.shippingAddressState}
                    onChange={onChange}
                  />
                </div>
              </div>
            </div>
            <div className="col-12">
              <div className="grid">
                <div className="col">
                  <label htmlFor="BillingPostalCode">Postal Code</label>
                </div>
                <div className="col">
                  <InputText
                    id="BillingPostalCode"
                    name="billingAddressPostalcode"
                    maxLength={20}
                    value={item.billingAddressPostalcode}
                    onChange={onChange}
                  />
                </div>
                <div className="col-fixed p-fixed-10 hidden-xs">
                  <Button
                    icon="pi pi-arrow-circle-left"
                    className="p-button-rounded p-button-text p-button-lg"
                    tooltip="Copy Shipping Address"
                    onClick={onCopyShippingClick}
                  />
                </div>
                <div className="col hidden-xs">
                  <label htmlFor="ShippingPostalCode1">Postal Code</label>
                </div>
                <div className="col hidden-xs">
                  <InputText
                    id="ShippingPostalCode1"
                    name="shippingAddressPostalcode"
                    type="text"
                    maxLength={20}
                    value={item.shippingAddressPostalcode}
                    onChange={onChange}
                  />
                </div>
              </div>
            </div>
            <div className="col-12">
              <div className="grid">
                <div className="col">
                  <label htmlFor="BillingCountry">Country</label>
                </div>
                <div className="col">
                  <InputText
                    id="BillingCountry"
                    name="billingAddressCountry"
                    maxLength={255}
                    value={item.billingAddressCountry}
                    onChange={onChange}
                  />
                </div>
                <div className="col-fixed p-fixed-10 hidden-xs">&nbsp;</div>
                <div className="col hidden-xs">
                  <label htmlFor="ShippingCountry1">Country</label>
                </div>
                <div className="col hidden-xs">
                  <InputText
                    id="ShippingCountry1"
                    name="shippingAddressCountry"
                    maxLength={255}
                    value={item.shippingAddressCountry}
                    onChange={onChange}
                  />
                </div>
              </div>
            </div>
            <div className="col-12 hidden-sm hidden-md hidden-lg">
              <div className="grid">
                <div className="col">
                  <Button
                    icon="pi pi-arrow-circle-down"
                    className="p-button-rounded p-button-text p-button-lg"
                    tooltip="Copy Billing Address"
                    onClick={onCopyBillingClick}
                  />
                </div>
                <div className="col">
                  <Button
                    icon="pi pi-arrow-circle-up"
                    className="p-button-rounded p-button-text p-button-lg"
                    tooltip="Copy Shipping Address"
                    onClick={onCopyShippingClick}
                  />
                </div>
              </div>
            </div>
            <div className="col-12 hidden-sm hidden-md hidden-lg">
              <h4>Shipping Address</h4>
            </div>
            <div className="col-12 hidden-sm hidden-md hidden-lg">
              <div className="grid">
                <div className="col-12">
                  <label htmlFor="ShippingStreet2">Street</label>
                </div>
                <div className="col-12">
                  <InputText
                    id="ShippingStreet2"
                    name="shippingAddressStreet"
                    maxLength={150}
                    value={item.shippingAddressStreet}
                    onChange={onChange}
                  />
                </div>
              </div>
            </div>
            <div className="col-12 hidden-sm hidden-md hidden-lg">
              <div className="grid">
                <div className="col-12">
                  <label htmlFor="ShippingCity2">City</label>
                </div>
                <div className="col-12">
                  <InputText
                    id="ShippingCity2"
                    name="shippingAddressCity"
                    maxLength={100}
                    value={item.shippingAddressCity}
                    onChange={onChange}
                  />
                </div>
              </div>
            </div>
            <div className="col-12 hidden-sm hidden-md hidden-lg">
              <div className="grid">
                <div className="col-12">
                  <label htmlFor="ShippingState2">State</label>
                </div>
                <div className="col-12">
                  <InputText
                    id="ShippingState2"
                    name="shippingAddressState"
                    maxLength={100}
                    value={item.shippingAddressState}
                    onChange={onChange}
                  />
                </div>
              </div>
            </div>
            <div className="col-12 hidden-sm hidden-md hidden-lg">
              <div className="grid">
                <div className="col-12">
                  <label htmlFor="ShippingPostalCode2">Postal Code</label>
                </div>
                <div className="col-12">
                  <InputText
                    id="ShippingPostalCode2"
                    name="shippingAddressPostalcode"
                    maxLength={20}
                    value={item.shippingAddressPostalcode}
                    onChange={onChange}
                  />
                </div>
              </div>
            </div>
            <div className="col-12 hidden-sm hidden-md hidden-lg">
              <div className="grid">
                <div className="col-12">
                  <label htmlFor="ShippingCountry2">Country</label>
                </div>
                <div className="col-12">
                  <InputText
                    id="ShippingCountry2"
                    name="shippingAddressCountry"
                    maxLength={255}
                    value={item.shippingAddressCountry}
                    onChange={onChange}
                  />
                </div>
              </div>
            </div>
          </div>
        </Card>
        <div className="grid">
          <div className="col-12">&nbsp;</div>
        </div>
        <Card>
          <div className="grid">
            <div className="col-12">
              <h3>Terms &amp; Conditions</h3>
            </div>
          </div>
          <div className="grid">
            <div className="col-12">
              <InputTextarea
                name="termsConditions"
                value={item.termsConditions}
                onChange={onChange}
                style={{ width: 280 }}
                autoResize
              />
            </div>
          </div>
        </Card>
        <div className="grid">
          <div className="col-12">&nbsp;</div>
        </div>
        <Card>
          <div className="grid">
            <div className="col-12">
              <h3>Description</h3>
            </div>
          </div>
          <div className="grid">
            <div className="col-12">
              <InputTextarea
                name="description"
                value={item.description}
                onChange={onChange}
                style={{ width: 280 }}
                autoResize
              />
            </div>
          </div>
        </Card>
        <Card>
          <div className="grid">
            <div className="col text-left">
              <h3>Items</h3>
            </div>
            <div className="col text-right">
              <Button
                icon="pi pi-plus"
                className="p-button-rounded p-button-outlined"
                tooltip="Add Purchase Order Item"
                onClick={onAddClick}
              />
            </div>
          </div>
          <div className="grid">
            <div className="col-12">
              <DataTable
                value={item.items}
                editMode="row"
                dataKey="id"
                onRowEditComplete={onRowEditComplete}
                reorderableRows
                onRowReorder={onRowReorder}
                footer={tableFooter}
              >
                <Column rowReorder style={{ width: "3em" }} />
                <Column
                  field="productId"
                  header="Item Name"
                  body={productBodyTemplate}
                  editor={(options) => productEditor(options)}
                ></Column>
                <Column
                  field="comment"
                  header="Comment"
                  editor={(options) => textEditor(options)}
                ></Column>
                <Column
                  field="quantity"
                  header="Quantity"
                  editor={(options) => numberEditor(options)}
                ></Column>
                <Column
                  field="listPrice"
                  header="Selling Price"
                  body={priceBodyTemplate}
                  editor={(options) => numberEditor(options)}
                ></Column>
                <Column
                  field="discountType"
                  header="Discount"
                  body={discountBodyTemplate}
                  editor={(options) => discountEditor(options)}
                ></Column>
                <Column
                  field="total"
                  header="Total"
                  body={totalBodyTemplate}
                ></Column>
                <Column
                  rowEditor
                  headerStyle={{ minWidth: "2rem" }}
                  bodyStyle={{ textAlign: "center" }}
                ></Column>
                <Column
                  body={actionBodyTemplate}
                  style={{ minWidth: "2rem", textAlign: "center" }}
                ></Column>
              </DataTable>
            </div>
          </div>
        </Card>
        <div className="grid">
          <div className="col-12">&nbsp;</div>
          <div className="col-12">
            <Button
              icon="pi pi-check"
              className="p-button-rounded p-button-outlined"
              tooltip="Save"
              disabled={!valid}
              onClick={onSaveClick}
            />
            &nbsp; &nbsp;
            <Button
              icon="pi pi-times"
              className="p-button-rounded p-button-outlined"
              tooltip="Back to List"
              onClick={onCancelClick}
            />
            &nbsp; &nbsp;
            <Button
              icon="pi pi-print"
              className="p-button-rounded p-button-outlined"
              tooltip="Print"
              onClick={onPrintClick}
            />
          </div>
        </div>
        <Dialog
          visible={deleteDialog}
          style={{ width: "450px" }}
          header="Confirm"
          modal
          footer={deleteDialogFooter}
          onHide={hideDeleteDialog}
        >
          <div className="confirmation-content">
            <i
              className="pi pi-exclamation-triangle mr-3"
              style={{ fontSize: "2rem" }}
            />
            <strong>Are you sure you want to delete this item?</strong>
          </div>
        </Dialog>
      </div>
      <div className="p-hidden">
        <div ref={componentRef}>
          <PurchaseOrderPrint
            item={item}
            currencies={currencies}
            products={products}
            vendors={vendors}
          />
        </div>
      </div>
    </>
  );
};

export default PurchaseOrderEdit;
